.app {
  background-color: black;
  color: white;
  font-family: Arial, sans-serif;
  text-align: center;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
}

.balance {
  position: absolute;
  top: 10px;
  right: 20px;
  font-size: 20px;
  font-weight: bold;
  color: yellow;
}

h1 {
  font-size: 24px;
  margin-bottom: 10px;
}

.gif-container {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.miner-gif {
  width: 300px;
  height: 300px;
}

.coins {
  font-size: 20px;
  margin-bottom: 15px;
  font-weight: bold;
  color: yellow;
}

.collect-button {
  background-color: #ffcc00;
  color: black;
  font-size: 16px;
  padding: 10px 20px;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  transition: background 0.3s, transform 0.2s;
}

.collect-button:hover {
  background-color: #ffdd44;
  transform: scale(1.1);
}

.collect-button:active {
  transform: scale(0.95);
}
